import config from './config.js'

const apiResource = {
  UserLogin: '/api/UserService/UserLogin',//

  UserChangePassWord: '/api/Sys_UserService/UserChangePassWord',//

  FaceTest: '/api/UserService/BaiduFaceSearch',//

  GetUserInfo: '/api/Sys_UserService/GetUserInfo',//

  GetQualityCheckListPageBySqlWhere: '/api/Shop_QualityCheckService/GetQualityCheckListPageBySqlWhere',//

  GetMakeOrderByMachineCodeOrName: '/api/Order_WorkOrderService/GetMakeOrderByMachineCodeOrName',//

  GetMachineByCodeOrName: '/api/MachineService/GetMachineByCodeOrName',//

  GetProcessParamList: '/api/Basic_ProcessService/GetProcessParamList',//

  GetQualityCheckImgInfo: '/api/Shop_QualityCheckService/GetQualityCheckImgInfo',//

  AddQualityCheck: '/api/Shop_QualityCheckService/AddQualityCheck',//

  UpdateQualityCheck: '/api/Shop_QualityCheckService/UpdateQualityCheck',//

  DeleteQualityCheck: '/api/Shop_QualityCheckService/DeleteQualityCheck',//

  GetSysNotifyIconDto: '/api/immsgservice/GetSysNotifyIconDto',//

  GetUnReadNotifyListByIconType: '/api/immsgservice/GetUnReadNotifyListByIconType',//

  GetPalletTraceBack: '/api/DashboardService/GetPalletTraceBack',//
//=====================================================================================
  GetAllWorkshop:'/api/DashboardService/GetAllWorkshop',//
  GetWorkshopWasteQtySort:'/api/DashboardService/GetWorkshopWasteQtySort',//30
  GetWorkshopSubmitQtySort:'/api/DashboardService/GetWorkshopSubmitQtySort',//30
  GetWorkshopMachineStat:'/api/DashboardService/GetWorkshopMachineStat',//
  GetWorkshopMonthOutput:'/api/DashboardService/GetWorkshopMonthOutput',//30
  GetWorkshopTodayCapacity:'/api/DashboardService/GetWorkshopTodayCapacity',///
  GetWorkshopKPI:'/api/DashboardService/GetWorkshopKPI',//KPI
  GetWorkshopInOutRatio:'/api/DashboardService/GetWorkshopInOutRatio',//12
  GetWorkshopWaitProd:'/api/DashboardService/GetWorkshopWaitProd',//
//=====================================================================================
  GetWorkshopMachineRuntimeStat:'/api/DashboardService/GetWorkshopMachineRuntimeStat',//
}

const setBaseUrl = (name) => {
  if (name == 'FaceTest') {
    let baseUrl = "http://192.168.202.9:8860";
    return baseUrl.concat(apiResource[name]);
  }
  return config.getBaseUrl().concat(apiResource[name]);
}

const resources = {
  UserLoginResource: () => { return setBaseUrl('UserLogin') },
  UserChangePassWordResource: () => { return setBaseUrl('UserChangePassWord') },
  FaceTestResource: () => { return setBaseUrl('FaceTest') },
  GetUserInfoResource: () => { return setBaseUrl('GetUserInfo') },
  GetQualityCheckListPageBySqlWhereResource: () => { return setBaseUrl('GetQualityCheckListPageBySqlWhere') },
  GetMakeOrderByMachineCodeOrNameResource: () => { return setBaseUrl('GetMakeOrderByMachineCodeOrName') },
  GetMachineByCodeOrNameResource: () => { return setBaseUrl('GetMachineByCodeOrName') },
  GetProcessParamListResource: () => { return setBaseUrl('GetProcessParamList') },
  GetQualityCheckImgInfoResource: () => { return setBaseUrl('GetQualityCheckImgInfo') },
  AddQualityCheckResource: () => { return setBaseUrl('AddQualityCheck') },
  UpdateQualityCheckResource: () => { return setBaseUrl('UpdateQualityCheck') },
  DeleteQualityCheckResource: () => { return setBaseUrl('DeleteQualityCheck') },
  GetSysNotifyIconDtoResource: () => { return setBaseUrl('GetSysNotifyIconDto') },
  GetUnReadNotifyListByIconTypeResource: () => { return setBaseUrl('GetUnReadNotifyListByIconType') },
  GetPalletTraceBackResource: () => { return setBaseUrl('GetPalletTraceBack') },
  //=====================================================================================
  GetAllWorkshopResource: () => { return setBaseUrl('GetAllWorkshop') },
  GetWorkshopWasteQtySortResource: () => { return setBaseUrl('GetWorkshopWasteQtySort') },
  GetWorkshopSubmitQtySortResource: () => { return setBaseUrl('GetWorkshopSubmitQtySort') },
  GetWorkshopMachineStatResource: () => { return setBaseUrl('GetWorkshopMachineStat') },
  GetWorkshopMonthOutputResource: () => { return setBaseUrl('GetWorkshopMonthOutput') },
  GetWorkshopTodayCapacityResource: () => { return setBaseUrl('GetWorkshopTodayCapacity') },
  GetWorkshopKPIResource: () => { return setBaseUrl('GetWorkshopKPI') },
  GetWorkshopInOutRatioResource: () => { return setBaseUrl('GetWorkshopInOutRatio') },
  GetWorkshopWaitProdResource: () => { return setBaseUrl('GetWorkshopWaitProd') },
  //=====================================================================================
  GetWorkshopMachineRuntimeStat: () => { return setBaseUrl('GetWorkshopMachineRuntimeStat') },
}

export default resources



