<!---->
<template>
  <div class="aside" :class="{'aside-hide': isAsideHide}">
    <div class="input-group">
      <h4></h4>
      <label for="designW">():</label>
      <input id="designW" v-model="designW" type="number" min="1" />
      <label for="designH">():</label>
      <input id="designH" v-model="designH" type="number" min="1" />
    </div>
    <div class="input-group other">
      <h4></h4>
      <label for="getBaseUrl">:</label>
      <input id="getBaseUrl" style="width: 15rem;" type="text" v-model="getBaseUrl" />
      <br />
      <label for="getPageInterval">():</label>
      <input id="getPageInterval" type="number" v-model="pageRotation" />
      <br />
      <label for="getStatisKPI">KPI():</label>
      <input id="getStatisKPI" type="number" v-model="statisticalKPI" />
      <br />
      <label for="getRealKPI">KPI():</label>
      <input id="getRealKPI" type="number" v-model="realTimeKPI" />
      <br />
      <label for="getComInterval">():</label>
      <input id="getComInterval" type="number" v-model="componentPageTurning" />
      <br />
      <label for="getTextSpeed">:</label>
      <input id="getTextSpeed" type="number" v-model="textScrollingSpeed" />
      <br />
      <label for="getDashboardSpeed">:</label>
      <input id="getDashboardSpeed" type="number" v-model="dashboardSpeed" />
      <br />
      <label for="getDevWait">():</label>
      <input id="getDevWait" type="number" v-model="devWait" />
      <br />
    </div>
    <div class="input-group other">
      <h4></h4>
      <input name="" type="checkbox" v-model="isCheckAll" id="all" @click="cli" /> 
      <br />
      <span>:</span>
      <br />
      <div class="con">
        <span
          style="display:inline-block; width: 42%;"
          v-for="(item, index) in workshopList"
          :key="index"
        >
          <input
            name="cc"
            type="checkbox"
            :value="item.WorkshopName"
            v-model="checkboxList"
          />
          {{item.WorkshopName}}
        </span><br>
      </div>
    </div>
    <div class="input-group other">
      <h4></h4>
      <span>:</span>
      <input id="theme-black" checked name="theme" type="radio" v-model="theme" value="" />
      <label for="theme-black"></label>
      <input id="theme-blue" name="theme" type="radio" v-model="theme" value="" />
      <label for="theme-blue"></label>
      <br />
      <!-- <span>:</span>
      <input
        id="table-row"
        checked
        name="table"
        type="radio"
        v-model="tableContentSwitching"
        value=""
      >
      <label for="table-row"></label>
      <input id="table-page" name="table" type="radio" v-model="tableContentSwitching" value="">
      <label for="table-page"></label>-->
    </div>
    <div class="input-group">
      <button @click="setConfig"></button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  props: {
    isAsideHide: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState([
      'config',
      'workshopList'
    ])
  },
  data() {
    return {
      designW: '',
      designH: '',
      checkboxList: '',
      isCheckAll: true, //
      getBaseUrl: '',
      pageRotation: '', //()
      statisticalKPI: '', //KPI()
      realTimeKPI: '', //KPI()
      componentPageTurning: '',//()
      textScrollingSpeed: '',//
      dashboardSpeed: '',//
      devWait: '',
      theme: '',//
      tableContentSwitching: '',//
    }
  },
  methods: {
    ...mapMutations([
      'saveConfig',
      'setWorkshopList'
    ]),
    initData() {
      this.designW = this.config.designW || 1920
      this.designH = this.config.designH || 1080
      this.pageRotation = this.config.pageRotation //()
      this.statisticalKPI = this.config.statisticalKPI//KPI()
      this.realTimeKPI = this.config.realTimeKPI //KPI()
      this.componentPageTurning = this.config.componentPageTurning//()
      this.textScrollingSpeed = this.config.textScrollingSpeed//
      this.textScrollingSpeed = this.config.textScrollingSpeed//
      this.theme = this.config.theme//
      this.devWait = this.config.devWait//
      this.dashboardSpeed = this.config.dashboardSpeed//
      this.checkboxList = this.config.checkboxList ? this.config.checkboxList : this.workshopList.map(item => item.WorkshopName)
      let settings = {
        checkboxList: this.checkboxList
      }
      this.saveConfig(settings)
    },
    setConfig() {
      let settings = {
        baseUrl: this.getBaseUrl,
        designW: this.designW,
        designH: this.designH,
        checkboxList: this.checkboxList,
        pageRotation: this.pageRotation, //()
        statisticalKPI: this.statisticalKPI, //KPI()
        devWait: this.devWait, //seamless
        realTimeKPI: this.realTimeKPI, //KPI()
        componentPageTurning: this.componentPageTurning,//()
        textScrollingSpeed: this.textScrollingSpeed,//
        dashboardSpeed: this.dashboardSpeed,//
        theme: this.theme,//
        tableContentSwitching: this.tableContentSwitching,//
      }
      //
      // if (this.checkboxList.length === 0) {
      //   this.$emit('showAside')
      // }
      this.saveConfig(settings)
      this.$emit('setConfig') //pageresize,scalelocalstorage
    },
    cli() {
      this.isCheckAll = !this.isCheckAll
      if (this.isCheckAll) {
        this.checkboxList = this.workshopList.map(item => item.WorkshopName)
      } else {
        this.checkboxList = []
      }
    }
  },
  watch: {
    workshopList: {
      handler(newV, oldV) {
        this.initData()
      },
      deep: true
    }
  },
  mounted() {
    this.getBaseUrl = this.config.baseUrl
  }
}
</script>

<style scoped>
.aside {
  position: absolute;
  z-index: 100;
  color: #fff;
  left: 0;
  bottom: 2rem;
  border: 0.5rem solid transparent;
  background: midnightblue;
  width: 30rem;
  border-radius: 0 1rem 1rem 0;
  box-shadow: 0 0 0.5rem 0.5rem deepskyblue;
  transition: all 0.3s ease;
}
.aside:after {
  content: "";
  display: block;
  position: absolute;
  width: 1rem;
  height: 100%;
  top: 0;
  right: 0;
  transform: translateX(2rem);
}
.aside-hide {
  transform: translateX(-32rem);
}
.input-group ~ .input-group {
  border-top: 0.0625rem solid rgba(135, 206, 235, 0.63);
}
.input-group {
  padding: 1rem 0.5rem;
}
.input-group:last-child {
  text-align: center;
}
h4 {
  margin-bottom: 0.5rem;
}
label {
  margin: 0 0.5rem 0 1rem;
  white-space: nowrap;
}
.input-groupcolors label {
  margin-left: 0;
}
.input-group.other label {
  line-height: 2.5rem;
}
.input-group.other span {
  margin: 0 0.5rem 0 1rem;
}
.input-group.other #all {
  margin-left: 1rem;
}
input {
  padding: 0.125rem 0.25rem;
  width: 6rem;
  font-size: 1.125rem;
  color: #fff;
  border: 0.0625rem solid rgba(255, 255, 255, 0.5);
  background: transparent;
}
input[type="radio"],
input[type="checkbox"] {
  width: auto;
  height: 1rem;
  vertical-align: top;
}
input[type="checkbox"] {
  position: relative;
  top: 0.3rem;
}
#theme-black,
#theme-blue,
#table-row,
#table-page {
  position: relative;
  top: 0.8rem;
  left: 0.3rem;
}
button {
  margin-top: 0.5rem;
  padding: 0.25rem 1rem;
  font-size: 1.25rem;
  color: #fff;
  background: #2196f3;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
}
button:hover {
  background: #4ea3d6;
}
button:active {
  background: #4e93c6;
}
</style>


